body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
    height: 100%;
}

html {
    display: table;
    margin: auto;
}

body {
  display: table-cell;
  vertical-align: middle;
  max-width: 1290px;
  margin: auto;
  background-color: darkslategray;
}

.App-header {
  text-align: center;
  color: navajowhite;
}

.Player-list-box-div {
  display: -webkit-flex;
  display: flex;
  float: left;
}

.Player-list-box {
  background-color: lightblue;
}

.Player-button {;
  display: -webkit-flex;;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-left: 1%;
  float: left;
}

.Add-button {;
  width: 80px;
  background-color: green;
}

.Remove-button {;
  width: 80px;
  background-color: red;
}

.Clear-button {;
  width: 80px;
  background-color: yellow;
}

.User-list-box-div {
  display: -webkit-flex;
  display: flex;
  margin-left: 1%;
  float: left;
}

.User-list-box {
  background-color: lightblue;
}

.Draft-button-div {;
  margin-left: 1%;
  float: left;
}

.Draft-button {;
  width: 80px;
  background-color: orange;
}

.Draft-results-div {;
  display: -webkit-flex;;
  display: flex;
  margin-left: 1%;
  float: left;
}

.Draft-results {;
  background-color: lightblue;
}

.Slider-div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  float: left;
}

